import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import ImageListItem from "@mui/material/ImageListItem";

const dispatchedEvents = [];

const SrsMuiImageListItem = function (props, ref) {
  return (
    <ImageListItem {...props} theme={props.muiTheme}>
      <slot />
    </ImageListItem>
  );
};

const styles = `
  /* Custom styles for the MuiImageListItem component */
`;

const props = [
  {
    name: "cols",
    defaultValue: 1,
    type: "number",
  },
  {
    name: "rows",
    defaultValue: 1,
    type: "number",
  },
  {
    name: "onClick",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to ImageListItem if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiImageListItem),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
